<template lang="pug">
    .popup-form
        header.popup-form__header.popup-form__header_pb10
            .popup-form__title Регистрация
            .popup-form__steps
                .sm-info-box Шаг {{regStep}}/2
                //- span.btn.btn_small(v-if="regStep == 1" @click.prevent="regStep = 2") Активировать профиль
            .popup-form__close
                .close-btn(@click.prevent="closePopups(); regStep=1 ")

        form(@submit.prevent='regSendData()' data-vv-scope='reg-form').popup-form__main(v-if="regStep == 1")
            .form-grid
                .form-grid__item
                    input(
                        type="text"
                        name='firstName'
                        placeholder="Имя администратора"
                        v-validate="'required'"
                        v-model="formData.firstName"
                    ).st-input
                .form-grid__item
                    input(
                        type="text"
                        name='lastName'
                        placeholder="Фамилия администратора"
                        v-validate="'required'"
                        v-model="formData.lastName"
                    ).st-input
                .form-grid__item
                    input(
                        type="text"
                        name='middleName'
                        placeholder="Отсчество администратора"
                        v-validate="'required'"
                        v-model="formData.middleName"
                    ).st-input
                .form-grid__item
                    input(
                        type="text"
                        name='phoneNumber'
                        placeholder="Телефон"
                        v-validate="'required'"
                        v-model="formData.phoneNumber"
                    ).st-input
                .form-grid__item
                    input(
                        type="text"
                        name='companyName'
                        placeholder="Название компании"
                        v-validate="'required'"
                        v-model="formData.companyName"
                    ).st-input
                .form-grid__item
                    input(
                        type="text"
                        name='email'
                        placeholder="E-mail"
                        v-validate="'required'"
                        v-model="formData.email"
                    ).st-input
                .form-grid__item
                    input(
                        type="password"
                        name='password'
                        placeholder="Пароль"
                        v-validate="'required'"
                        ref="password"
                    ).st-input
                .form-grid__item
                    input(
                        type="password"
                        name="password_confirmation"
                        placeholder="Пароль еще раз "
                        v-validate="'required|confirmed:password'"
                        v-model="formData.password"
                    ).st-input

            .popup-form__footer.tac
                button.btn Зарегистрироваться

        form(@submit.prevent='activateAcc()' data-vv-scope='code-form').popup-form__main(v-if="regStep == 2")
            .popup-form__descr Необходимо подтвердить адрес электронной почты. Для этого введите код высланный на почту.
            .form-grid(v-if="regStep == 2")
                .form-grid__item
                    input(
                        type="text"
                        name='activationCode'
                        placeholder="Код"
                        v-validate="'required'"
                        v-model="activationCode"
                    ).st-input

                .form-grid__item
                    button.btn.btn_block Подтвердить

</template>

<script>
import ApiService from '@/services/api.service'

export default {
  name: 'RegForm',
  props: {
    openPopup: Function,
    closePopups: Function
  },
  data () {
    return {
      regStep: 1,
      formData: {
        companyName: '',
        email: '',
        firstName: '',
        lastName: '',
        middleName: '',
        password: '',
        phoneNumber: ''
      },
      activationCode: '',
      userId: ''
    }
  },
  methods: {
    regSendData () {
      this.$validator.validateAll('reg-form').then(result => {
        if (result) {
          ApiService.post('registration', this.formData)
            .then((response) => {
              this.userId = response.data
              this.regStep = 2
            })
            .catch((response) => {
              alert('Такой аккаунт уже существует')
            })
        }
      })
    },
    activateAcc () {
      this.$validator.validateAll('code-form').then(result => {
        if (result) {
          ApiService.post(`registration/activate?userId=${this.userId}&activationCode=${this.activationCode}`)
            .then((response) => {
              console.log(response)
              this.openPopup('log')
            })
            .catch((response) => {
              console.log(response)
              alert('Не верный код')
            })
        }
      })
    }
  }
}

</script>
