<template lang="pug">
    form(@submit.prevent="sendForm()").app-st-box(v-if="mainCompanyInfo")
        .app-st-box__header
            .st-title.st-title_fz_30.st-title_color_lblue {{mainCompanyInfo.name}}

        .app-st-box__main.multi-form
            .multi-form__section 
                .multi-form__title.st-title.st-title_fz_18 Название компании
                input(
                    type='text'
                    v-model="formData.name" 
                    v-validate="'required'"
                ).st-input
            .multi-form__section 
                .multi-form__title.st-title.st-title_fz_18 Реквизиты
                input(
                    v-model="formData.inn" 
                    type='text'
                    v-validate="'required'" 
                ).st-input
            .multi-form__section 
                .multi-form__title.st-title.st-title_fz_18 Описание
                textarea(
                    v-validate="'required'" 
                    v-model="formData.details"
                ).st-textarea


        .app-st-box__footer
            button.btn Сохранить

</template>

<script>
import ApiService from "@/services/api.service" 
import * as R from "ramda"

export default {
    name: 'Requisites',
    data() {
        return {
            formData: ''
        }
    },
    computed: {
        mainCompanyInfo() {
            let storeData = this.$store.getters.getState('mainCompanyInfo')
            this.formData = R.clone(storeData)
            return storeData 
        }
    },
    methods: { 
        sendForm() {
            this.$validator.validateAll().then(result => {
                if (result) {
                    ApiService.post('company/details', this.formData).then(res => {
                        this.$store.dispatch('mainCompanyInfo') 
                        this.$notify({ 
                            group: 'default',
                            title: 'Уведомление',
                            text: 'Настройки сохранены'
                        });
                    }).catch(rej => {
                        console.log(rej)
                    })
                }
            });
        }
    }
}

</script>
