<template lang="pug"> 
    div
        .app-main__section(v-if="users.length")
            .app-st-box 
                .app-st-box__header
                    .app-st-box__title Сотрудники 

                .app-st-box__main 
                    .table-team  
                        .table-team__tr(v-for="userItem, userKey in users" :key="userKey")
                            .table-team__td
                                span.fz-18.bold {{userItem.lastName}} {{userItem.firstName}} {{userItem.middleName}}

                            .table-team__td
                                .btn-settings(@click.prevent="deleteUser(userItem.id)")
                                    font-awesome-icon(icon="trash-alt").btn-settings__icon
                                     

        .app-main__section
            form(@submit.prevent="addUser()").app-st-box
                .app-st-box__header
                    .app-st-box__title Приглашение кредитных специалистов
                .app-st-box__main.form-grid.form-grid_3el
                    .form-grid__item
                        input(
                            type='text' 
                            v-validate="'required'"
                            v-model="formData.firstName" 
                            placeholder='Имя'
                        ).st-input

                    .form-grid__item
                        input(
                            type='text' 
                            v-validate="'required'"
                            v-model="formData.lastName" 
                            placeholder='Фамилия'
                        ).st-input

                    .form-grid__item
                        input(
                            type='text' 
                            v-validate="'required'"
                            v-model="formData.middleName" 
                            placeholder='Отчество'
                        ).st-input

                    .form-grid__item
                        input(
                            type='text' 
                            v-validate="'required'"
                            v-model="formData.email" 
                            placeholder='E-Mail'
                        ).st-input

                    .form-grid__item
                        input(
                            type='text' 
                            v-validate="'required'"
                            v-model="formData.phoneNumber" 
                            placeholder='Телефон'
                        ).st-input

                .app-st-box__footer.tac
                    button.btn Отправить приглашение на E-mail

</template>

<script>
import ApiService from "@/services/api.service"
import fieldsNames from '@/enums/fieldsNames'


export default {
    name: "Team",  
    data() {
        return {
            formData: {
                email: '',
                firstName: '',
                lastName: '',
                middleName: '',
                phoneNumber: '',
                companyId: localStorage.getItem("companyId")
            },
            fieldsNames: fieldsNames
        }
    },
    computed: {
        users() {
            return this.$store.getters.getState('companyUsers')
        },  
    },
    methods: {
        addUser() {            
            this.$validator.validateAll().then(result => {
                if(result) {
                    ApiService.post(`user/create`, this.formData).then(res => {     
                        console.log(res)                    
                        this.$store.dispatch('companyUsers')
                    }).catch(rej => { 
                        this.$notify({
                            group: 'default',
                            title: 'Ошибка',
                            type: 'error',
                            text: 'Пользователь с таким e-mail уже существует'
                        });
                    })
                }
            })
        },
        deleteUser(id) { 
            ApiService.post(`user/delete?userId=${id}`).then(res => {
                this.$store.dispatch('companyUsers')
                this.$notify({
                    group: 'default',
                    title: 'Уведомление',
                    text: 'Пользователь удален'
                });
            }).catch(rej => {
                console.log(rej)
            })
        }
    }
};
</script>
