<template lang="pug">

    form(@submit.prevent="sendForm()").app-st-box(v-if="allParameters")
        .app-st-box__header
            .app-st-box__title Настройка модели

        .app-st-box__main.multi-form
            .multi-form__section
                .multi-form__title
                    .st-title.st-title_fz_18 Обязательные параметры
                .form-grid 
                    .form-grid__item(v-for="paramItem, paramKey in param.mandatory" :key="paramKey")
                        .st-input.st-input_parameter {{paramItem.name}}

            .multi-form__section
                .multi-form__title  
                    .st-title.st-title_fz_18 Выборочные параметры
                .form-grid
                    .form-grid__item(v-for="paramItem, paramKey in param.other" :key="paramKey") 
                        label.input-box.cup 
                            .input-box__checkbox
                                .st-checkbox  
                                    input(type="checkbox" v-model="selectedParam" :value="paramItem.name").st-checkbox__input
                                    <font-awesome-icon class='st-checkbox__icon' icon="check" />
                            .st-input.st-input_parameter {{paramItem.name}}

        .app-st-box__footer(v-if="allParameters")
            button.btn Сохранить 

        
</template>
  
<script>
import ApiService from "@/services/api.service" 
import * as R from "ramda"


export default {
    name: "ModelSettings", 
    data() { 
        return {
            selectedParam: '',
            equalsParam: true
        }
    },
    computed: { 
        allParameters() {
            return this.$store.getters.getState('allParameters')
        },
        param() {
            this.selectedParam = this.$store.getters.getState('selectedParam')
            let mandatory = R.filter(item => item.mandatory, this.allParameters)
            let other = R.filter(item => !item.mandatory, this.allParameters)

            // R.map(item => R.prop('name', item), mandatory)
            return { 
                mandatory: mandatory,
                other: other
            }  
        }
    },
    methods: { 
        sendForm() {
            let companyId = localStorage.getItem("companyId")
            
            ApiService.post(`parameter?companyId=${companyId}`, this.selectedParam).then(res => {
                this.$store.dispatch('companyParameters')
                this.$notify({ 
                    group: 'default',
                    title: 'Уведомление',
                    text: 'Настройки сохранены'
                });
            }).catch(rej => {
                console.log(rej)
            })
        }
    }
};
</script>
