<template lang="pug">
    form(@submit.prevent='submit()').popup-form
        header.popup-form__header
            .popup-form__title Вход в систему
            .popup-form__close
                .close-btn(@click.prevent="closePopups()")

        .popup-form__main
            .form-grid
                .form-grid__item
                    .form-group
                        input(
                            key="email"
                            type="text"
                            v-validate="'required'"
                            name="username"
                            :class="{'danger': errors.has('email')}"
                            v-model="username"
                            placeholder="E-mail"
                        ).st-input
                        //- .form-group__error(v-if="!$v.email.required") Обязательное поле
                        //- .form-group__error(v-if="!$v.email.email") Должен быть email
                .form-grid__item
                    .form-group
                        input(
                            key="password"
                            type="password"
                            v-validate="'required'"
                            name="password"
                            :class="{'danger': errors.has('password')}"
                            v-model="password"
                            placeholder="Пароль"
                        ).st-input
                        //- .form-group__error(v-if="!$v.password.required") Обязательное поле
                        
        .popup-form__line 
            .terms-text
                .st-checkbox(:class="{'error': errors.has('terms_use')}").terms-text__checkbox 
                    input(v-validate="'required'" type='checkbox' id='terms_use' name="terms_use" v-model="termsUse").st-checkbox__input
                    <font-awesome-icon class='st-checkbox__icon' icon="check" />

                label.terms-text__label(for='terms_use')  
                    | С 
                    span.terms-text__link(@click.prevent="openPopup('terms_use')") условиями использования сервиса
                    | 
                    | ознакомлен и согласен

        .popup-form__footer 
            button.btn Войти
            span(@click="openPopup('password')").st-link Забыли пароль?

</template>

<script>
import VueCookies from "vue-cookies";
import ApiService from "@/services/api.service";
import tokenService from "@/services/token.service";

export default {
    name: "LoginForm",
    props: {
        openPopup: Function,
        closePopups: Function
    },
    data() {
        return {
            termsUse: '',
            username: "",
            password: ""
        };
    },
    methods: { 
        submit() { 
            this.$validator.validateAll().then(result => {
                if (result) {
                    ApiService.post(
                        "auth/login",
                        { username: this.username, password: this.password },
                        { emulateJSON: true }
                    )
                        .then(response => { 
                            console.log(response);
                            let token = response.body.csrfToken;
                            let role = response.body.authorities[0].role;
                            let companyId = response.body.authorities[0].companyId;

                            tokenService.saveToken(token);
                            ApiService.setHeader();

                            localStorage.setItem("role", role);
                            localStorage.setItem("companyId", companyId);
                            localStorage.setItem("userName", this.username); 

                            ApiService.companyId = companyId
                            
                            if (role == "ADMIN") {
                                this.$router.push("/manager"); 
                            } else {
                                this.$router.push("/user");
                            }
                        })
                        .catch(response => {
                            console.log("response", response);
                            alert("Не верный логин или пароль");
                        });

                    // this.mainFormSent = !this.mainFormSent

                    // var credentials = {
                    //     login: this.credentials.login,
                    //     password: this.credentials.password
                    // }
                    // // We need to pass the component's this context
                    // // to properly make use of http in the auth service
                    // auth.login(this, credentials, function (error) {
                    //     this.mainFormSent = !this.mainFormSent
                    //     this.errorLogin = true
                    // })
                }
            });
        }, 
    }
};
</script>
