import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

import App from '@/layouts/App.vue'

import ModelSetting from '@/views/ModelSettings.vue'
import Archive from '@/views/Archive.vue'
import ManagerProfile from '@/views/ManagerProfile.vue'
import Requisites from '@/views/Requisites.vue'
import Scoring from '@/views/Scoring.vue'  
import Team from '@/views/Team.vue'  
import ChangePassword from './views/user/ChangePassword.vue'



Vue.use(Router) 

import { getToken } from "@/services/token.service";

let router = new Router({
    mode: 'history', 
    linkActiveClass: 'active',
    linkExactActiveClass: 'exact-active',
    base: 'cash-forms',
    routes: [
        {
            path: '*',
            redirect: to => { 
                if (!getToken) {
                    return '/'
                } 
                if (localStorage.getItem("role") == "ADMIN") { 
                    return '/manager'
                } else {
                    return '/user'
                }
            }
        },
        {
            path: '/',
            name: 'home',
            component: Home
        },
        
        {
            path: '/user',
            name: 'user',
            component: App,
            meta: { 
                requiresAuth: true
            },
            children: [
                {
                    path: '',
                    redirect: '/user/scoring'
                },
                {
                    path: 'profile',
                    name: 'user-profile',
                    component: ChangePassword
                },
                {
                    path: 'archive',
                    name: 'user-archive',
                    component: Archive
                },
                {
                    path: 'scoring',
                    name: 'user-scoring',
                    component: Scoring
                }
            ]
        },
        {
            path: '/manager',
            name: 'manager',
            component: App,
            props: {
                type: 'manager' 
            },
            children: [
                {
                    path: '/',
                    redirect: '/manager/profile'
                },
                {
                    path: 'profile',
                    name: 'profile', 
                    component: ManagerProfile
                }, 
                {
                    path: 'model',
                    name: 'model',
                    component: ModelSetting
                },
                {
                    path: 'archive',
                    name: 'archive', 
                    component: Archive
                },
                {
                    path: 'requisites',
                    name: 'requisites',
                    component: Requisites
                },
                {
                    path: 'scoring',
                    name: 'scoring',
                    component: Scoring
                },
                {
                    path: 'team',
                    name: 'team',
                    component: Team
                },
                {
                    path: 'change-password',
                    component: ChangePassword
                }
            ]
        },
    ]
})

export default router
