import Vue from 'vue'
import VueResource from 'vue-resource'
import tokenService from '@/services/token.service'
import { API_URL } from '@/services/config'

import * as R from 'ramda'

const ApiService = { 
    init() {
        Vue.use(VueResource)
        Vue.http.options.root = API_URL
        // Vue.http.options.emulateJSON = true
        Vue.http.options.credentials = true
        Vue.http.options.xhr = { withCredentials: true } 
        
        this.companyId = localStorage.getItem("companyId")
        if (tokenService.getToken()) {
            this.setHeader()
        }
    },

    setHeader() {
        Vue.http.headers.common['X-CSRF-TOKEN'] = `${tokenService.getToken()}`
    },

    query(resource, params) {
        return Vue.axios.get(resource, params).catch(error => {
            throw new Error(`[RWV] ApiService ${error}`)
        })
    },

    get(resource, slug = '') {
        let path = slug ? `${resource}/${slug}` : resource
        return Vue.http.get(path).catch(error => {
            throw new Error(`[RWV] ApiService ${error}`)
        })
    },

    post(resource, params, headers) {
        return Vue.http.post(`${resource}`, params, headers)
    }, 

    update(resource, slug, params) {
        return Vue.axios.put(`${resource}/${slug}`, params)
    },

    put(resource, params) {
        return Vue.axios.put(`${resource}`, params)
    },

    delete(resource) {
        return Vue.axios.delete(resource).catch(error => {
            throw new Error(`[RWV] ApiService ${error}`)
        })
    },

    getMainCompanyInfo() { 
        return Vue.http.get(`company/${this.companyId}`)
    },

    getCompanyStatistics() {
        return Vue.http.get(`company/${this.companyId}/statistics`)
    },

    getCompanyTariff() {
        return Vue.http.get(`company/${this.companyId}/tariff`)
    },

    getCompanyUsers() {
        return Vue.http.get(`company/${this.companyId}/users`)
    },

    getCompanyParameters() {
        return Vue.http.get(`parameter?companyId=${this.companyId}`)
    },

    getAllParameters() {
        return Vue.http.get(`parameter/all`)
    }
}

export default ApiService
