<template lang="pug">
    .app-header
        .app-header__section
            .app-header__line(v-if="mainCompanyInfo")  
                //- .app-header__company-name {{mainCompanyInfo.name}} 
                .app-header__tariff 
                    .header-tariff 
                        .header-tariff__box 
                            .header-tariff__box-title Компания:
                            .header-tariff__box-value {{mainCompanyInfo.name}} 
                        .header-tariff__box 
                            .header-tariff__box-title Тариф:
                            .header-tariff__box-value {{companyTariff.name}} 
                        .header-tariff__box 
                            .header-tariff__box-title Проверок осталось:
                            .header-tariff__box-value {{companyTariff.currentRequests}} 
                        .header-tariff__box 
                            .header-tariff__box-title Действует до: 
                            .header-tariff__box-value {{companyTariff.expirationDate | moment("DD.MM.YYYY")}} 

            ul.menu-list(v-if="statusApp === 'ADMIN'") 
                li.menu-list__item(v-for="menuItem, menuKey in adminMenu" :key="menuKey") 
                    router-link(:to="{name: menuItem.route}").btn.btn_bor_blue {{menuItem.title}}
            ul.menu-list(v-if="statusApp === 'USER'")
                li.menu-list__item(v-for="menuItem, menuKey in userMenu" :key="menuKey") 
                    router-link(:to="{name: menuItem.route}").btn.btn_bor_blue {{menuItem.title}}

            //- .app-header__logo-box
            //-     .app-header__menu
            //-         .menu
            //-             .menu__button
            //-                 .hamburger
            //-                     i.hamburger__line
            //-                     i.hamburger__line
            //-                     i.hamburger__line
            //-             .menu__dropdown
            //-                 .menu__dropdown-inner

                //- .app-header__logo
                //-     .logo
                //-         p Cash
                //-         p.text--blue Machine
 
        //- .app-header__section
        //-     .number-checks
        //-         .number-checks__title
        //-             | Осталось проверок:
        //-             span.count-box 5
        //-         .number-checks__button
        //-             span.btn.btn_bor_blue Пополнить 

        .app-header__section 
            .header-events 
                .header-events__item
                    .btn.btn_bor_blue.btn_icon.btn_h_47(@click="signOut()")
                        span.btn__text Выйти
                        .btn__icon.btn__icon_ml_15
                            <font-awesome-icon class='event-button__icon' icon="sign-out-alt" />

</template>

<script>
import VueCookies from "vue-cookies";


let adminMenu = [
    {
        title: 'Профиль',
        route: 'profile'
    },   
    {
        title: 'Реквизиты',
        route: 'requisites'
    },
    {
        title: 'Моя команда',
        route: 'team'
    },
    {
        title: 'Настройка модели', 
        route: 'model'
    },
    {
        title: 'Проверка заемщика',
        route: 'scoring'
    },
    {
        title: 'Архив проверок',
        route: 'archive'
    },
]

let userMenu = [
    {
        title: 'Проверка заемщика',
        route: 'user-scoring'
    },
    {
        title: 'Архив',
        route: 'user-archive'
    },
    {
        title: 'Профиль',
        route: 'user-profile'
    },
    
]
export default {
    name: "AppHeader",
    data() {
        return {
            statusApp: localStorage.getItem("role"),
            adminMenu: adminMenu,
            userMenu: userMenu
        };
    },
    computed: {
        mainCompanyInfo() {
            return this.$store.getters.getState('mainCompanyInfo')
        },
        companyTariff() {
            return this.$store.getters.getState('companyTariff')
        }
    },
    methods: {
        signOut() {
            localStorage.clear()
            this.$router.push("/");
        }
    }
};
</script>


<style lang="sass" scoped>
.header-tariff 
    &__box 
        display: flex 
        align-items: center 
        margin-bottom: 5px 
 
    &__box-value
        font-size: 18px 
        font-family: 'GSB'
        margin-left: 10px 

</style>
