<template lang="pug">
    .profile-item {{data}}
        .profile-item__section
            .profile-item__grid
                .profile-item__grid-item
                    .profile-info-box
                        .profile-info-box__title {{fieldsNames.username}}
                        .profile-info-box__value {{data.username}}

                .profile-item__grid-item
                    .profile-info-box
                        .profile-info-box__title {{fieldsNames.creationDate}}
