                .profile-item__grid-item
                    .profile-info-box
                        .profile-info-box__title {{fieldsNames.updateTimestamp}}
                        .profile-info-box__value {{data.updateTimestamp | moment("DD.MM.YYYY") }}

                .profile-item__grid-item
                    .profile-info-box
                        .profile-info-box__title {{fieldsNames.status}}
                        .profile-info-box__value {{fieldsNames[data.status]}}

        .profile-item__section
            .profile-item__grid
                .profile-item__grid-item(v-for="sectionItem, sectionKey in data.parameters" :key="sectionKey")
                    .profile-info-box
                        .profile-info-box__title {{fieldsNames[sectionItem.name]}}
                        .profile-info-box__value {{sectionItem.name == 'sex' ? fieldsNames[sectionItem.value] : sectionItem.value}}

