<template lang="pug">
    .app-main__section
        .app-st-box
            .archive-header
                .archive-header__search-box
                    .archive-header__title
                        .app-st-box__title Архив
                    .archive-header__search {{searchValue}}
                        .search-box
                            .search-box__icon
                                svg(
                                    xmlns="http://www.w3.org/2000/svg"
                                    xmlns:xlink="http://www.w3.org/1999/xlink"
                                    width="13px" height="13px"
                                )
                                    path(
                                        fill-rule="evenodd"
                                        fill="rgb(0, 0, 0)"
                                        d="M12.826,12.062 L9.704,8.941 C10.478,7.997 10.944,6.789 10.944,5.472 C10.944,2.451 8.493,-0.000 5.472,-0.000 C2.449,-0.000 0.000,2.451 0.000,5.472 C0.000,8.493 2.449,10.944 5.472,10.944 C6.789,10.944 7.995,10.480 8.938,9.707 L12.060,12.826 C12.272,13.038 12.614,13.038 12.826,12.826 C13.038,12.616 13.038,12.272 12.826,12.062 ZM5.472,9.855 C3.053,9.855 1.087,7.889 1.087,5.472 C1.087,3.055 3.053,1.086 5.472,1.086 C7.889,1.086 9.858,3.055 9.858,5.472 C9.858,7.889 7.889,9.855 5.472,9.855 Z"
                                    )
                            input(v-model="searchValue" type='text' placeholder='Поиск по ФИО, серии и номеру паспорта, id заёмщика или кредита...').search-box__input.st-input.st-input_h_40.st-input_phc_

                .archive-header__selects
                    .archive-selects
                        .archive-selects__item.form-group.form-group_archive
                            .form-group__title Скоринг
                            .form-group__main
                                <v-select class='v-select_sm-white v-select_w_155' :searchable=false v-model="scoringSelect" :options="[{label: 'Не выбрано', value: 'all'},{label: 'Кредит выдан', value: true}, {label: 'Кредит не выдан', value: false}]"></v-select>
                        .archive-selects__item.form-group.form-group_archive.form-group_disabled(v-if="scoringSelect.value != true")
                            .form-group__title Статус
                            .form-group__main
                                <v-select class='v-select_sm-white' v-model="selected" :options="['foo','bar']"></v-select>
                        .archive-selects__item.form-group.form-group_archive(v-if="scoringSelect.value == true")
                            .form-group__title Статус
                            .form-group__main
                                <v-select class='v-select_sm-white' v-model="selected" :options="['foo','bar']"></v-select>
                        .archive-selects__item.form-group.form-group_archive(v-bind:class='')
                            .form-group__title Число дней просрочки
                            .form-group__main
                                <v-select class='v-select_sm-white v-select_w_155' v-model="selected" :options="['foo','bar']"></v-select>
            .app-st-box__arch-table
                .atchive-table
                    .atchive-table__item(v-for="archiveItem, archiveKey in archiveList" :key="archiveKey")
                        ProfileItem(:data="archiveItem")

                //- div {{filteredList}} {{scoringSelect.value}}

</template>

<script>
import ProfileItem from '@/components/ProfileItem.vue'
import * as R from 'ramda'

import vSelect from 'vue-select'

const profileList = [
  {
    name: 'Вартанян Вазген Алекбекович',
    photo: '../images/team/photo1.jpg',
    passport: '6343 884400',
    interestRate: '14',
    scoringStatus: true,
    idBorrower: '5636367884400',
    сreditЕerm: '36 месяцев',
    сreditStatus: true,
    сreditId: '5636367884400',
    сreditSum: '1 000 000 руб',
    overdue: '0'
  },
  {
    name: 'Вартанян Вазген Алекбекович',
    photo: '../images/team/photo1.jpg',
    passport: '6343 884400',
    interestRate: '14',
    scoringStatus: true,
    idBorrower: '5636367884400',
    сreditЕerm: '36 месяцев',
    сreditStatus: true,
    сreditId: '5636367884400',
    сreditSum: '1 000 000 руб',
    overdue: '0'
  },
  {
    name: 'Вартанян Вазген Алекбекович',
    photo: '../images/team/photo1.jpg',
    passport: '6343 884400',
    interestRate: '14',
    scoringStatus: false,
    idBorrower: '5636367884400',
    сreditЕerm: '36 месяцев',
    сreditStatus: true,
    сreditId: '5636367884400',
    сreditSum: '1 000 000 руб',
    overdue: '0'
  }
]

export default {
  created: function () {},
  name: 'Profile',
  components: {
    ProfileItem,
    'v-select': vSelect
  },
  data () {
    return {
      archiveList: '',
      profileList: profileList,
      searchValue: '',
      scoringSelect: { label: 'Не выбрано', value: 'all' },
      avatarImg: './images/team/photo7.jpg',
      selected: '',
      companyId: localStorage.getItem('companyId')
    }
  },
  mounted () {
    this.$http.get(`scoring/list?companyId=${this.companyId}`).then(
      response => {
        this.archiveList = response.data
        console.log(response)
      },
      err => {
        console.log(err)
      }
    )
  },
  computed: {
    filteredListSelected () {
      return R.filter(elem => {}, profileList)
    },
    filteredList () {
      return R.filter(elem => {
        return R.all(R.equals(true), [
          R.equals(elem.scoringStatus, this.scoringSelect.value),
          R.includes(
            R.toLower(this.searchValue),
            R.toLower(elem.name)
          )
        ])
      }, profileList)
    }
  },
  methods: {
    scoringSelectStatus () {
      if (this.scoringSelect.value !== true) {
        return 'form-group_disabled'
      } else {
        return ''
      }
    }
  }
}
</script>
