import Vue from 'vue'
import Vuex from 'vuex'

import ApiService from "@/services/api.service"
import * as R from 'ramda'

Vue.use(Vuex) 
 

export default new Vuex.Store({ 
    state: { 
        userInfo: '',

        mainCompanyInfo: '',
        companyStatistics: '',
        companyUsers: '',
        companyTariff: '',
        companyParameters: '',

        allParameters: '',
        selectedParam: ''
    },  
    getters: { 
        getState: state => name => {
            return state[name] 
        },
        companyInfo: (state) => {
            return state.companyInfo
        },
        userInfo: (state) => {
            return state.userInfo
        },
    }, 
    mutations: {
        updateState: (state, {name, value}) => {
            state[name] = value 
        },
        userInfo: (state, payload) => {
            state.userInfo = payload
        },
    }, 
    actions: { 
        async mainCompanyInfo ({commit}) {
            let data = await ApiService.getMainCompanyInfo()
            commit('updateState', {name: 'mainCompanyInfo', value: data.data})
        },
        async companyStatistics ({commit}) {
            let data = await ApiService.getCompanyStatistics()
            commit('updateState', {name: 'companyStatistics', value: data.data})
        },
        async companyTariff ({commit}) {
            let data = await ApiService.getCompanyTariff()
            commit('updateState', {name: 'companyTariff', value: data.data})
        },
        async companyUsers ({commit}) {
            let data = await ApiService.getCompanyUsers()
            commit('updateState', {name: 'companyUsers', value: data.data})
        },
        async companyParameters ({commit}) {
            let serverData = await ApiService.getCompanyParameters()
            let selectedParam = R.map(item => R.prop('name', item), serverData.data) 

            commit('updateState', {name: 'companyParameters', value: serverData.data})
            commit('updateState', {name: 'selectedParam', value: selectedParam})
        },
        async allParameters ({commit}) {
            let data = await ApiService.getAllParameters()
            commit('updateState', {name: 'allParameters', value: data.data})
        },
        async companyInfo ({ dispatch }) {
            await dispatch('mainCompanyInfo')
            await dispatch('companyStatistics')
            await dispatch('companyTariff')
            await dispatch('companyUsers')
            await dispatch('companyParameters')
            await dispatch('allParameters')
        },
        async userInfo ({ commit }) {
            let userName = localStorage.getItem("userName")
            let result = await ApiService.get(`user/find?name=${userName}`)  

            commit('userInfo', result.data)
        },
    }
})
