<script>
import Footer from "@/components/Footer.vue";

import LoginForm from "@/components/forms/LoginForm.vue";
import RegForm from "@/components/forms/RegForm.vue";

import data from "@/assets/data/data.json";
import ICountUp from "vue-countup-v2";
import { getToken } from "@/services/token.service";



const lang = "ru";

export default {
    name: "Home",
    components: {
        ICountUp,
        Footer,
        LoginForm,
        RegForm
    },
    data() {
        return {
            mobileMenuStatus: "closed",
            ICountUp: {
                startVal: 0,
                endVal: 88,
                decimals: 0,
                duration: 7,
                options: {
                    useEasing: true,
                    useGrouping: true,
                    separator: ",",
                    decimal: ".",
                    prefix: "",
                    suffix: "%"
                }
            },
            previewSection: data[lang].previewSection,
            whySection: data[lang].whySection,
            advantagesSection: data[lang].advantagesSection,
            startSection: data[lang].startSection,
            technologiesSection: data[lang].technologiesSection,
            teamSection: data[lang].teamSection,
            tariffsSection: data[lang].tariffsSection,
            popupOpened: ""
        };
    }, 
    methods: {
        onReady(instance, CountUp) {
            const that = this;
            instance.update(that.endVal + 100);
        },
        mobileMenuToggle() {
            this.mobileMenuStatus =
                this.mobileMenuStatus === "opened" ? "closed" : "opened";
        },
        handleSubmit() {
            this.regStep = 2;
        },
        openPopup(name) {
            this.popupOpened = name;
        },
        closePopups() {
            this.popupOpened = "";
        }
    },
    beforeRouteEnter: function(to, from, next) {
        if (!getToken()) {
            next() 
        } else { 
            if (localStorage.getItem("role") == "ADMIN") {
                next({path: 'manager/profile'})
            } else {
                next({path: 'user'})
            }
        }
    }
}; 
</script>
