<template lang="pug">
    .app-st-box
        .app-st-box__header
            .app-st-box__title Замена пароля

        .app-st-box__main
            .form.form_change-pass
                .form__main
                    .form__line
                        .form-group
                            .input-box
                                input(v-model.trim="$v.oldPassword.$model" type="password" placeholder="Текущий пароль").st-input
                                .input-box__eye
                                    <font-awesome-icon :icon="['far', 'eye']" />

                    .form__line
                        .form-group(:class="{ 'form-group--error': $v.newPassword.$error }")
                            .input-box
                                input(v-model.trim="$v.newPassword.$model" type="password" placeholder="Новый пароль").st-input
                                .input-box__eye
                                    <font-awesome-icon :icon="['far', 'eye']" />
                            .form-group__error(v-if="!$v.newPassword.minLength")
                                | Пароль должен содержать не менее {{ $v.newPassword.$params.minLength.min }} символов.

                    .form__line
                        .form-group(:class="{ 'form-group--error': $v.repeatPassword.$error }")
                            .input-box
                                input(v-model.trim="$v.repeatPassword.$model" type="password" placeholder="Повторите новый пароль").st-input
                                .input-box__eye
                                    <font-awesome-icon :icon="['far', 'eye']" />
                            .form-group__error(v-if="!$v.repeatPassword.sameAsPassword")
                                | Пароли должны быть идентичны.

                .form__footer
                    button.btn.btn_block Сохранить изменения

</template>

<script>
import { required, sameAs, minLength } from 'vuelidate/lib/validators'
import { validationMixin } from 'vuelidate'

export default {
  name: 'ChangePassword',
  mixins: [validationMixin],
  data () {
    return {
      oldPassword: '',
      newPassword: '',
      repeatPassword: ''
    }
  },
  methods: {
    // changeInputType: function(event) {
    //     console.log(event.target.parentElement.parentElement.children(input))
    // }
  },
  validations: {
    oldPassword: {
      required,
      minLength: minLength(6)
    },
    newPassword: {
      required,
      minLength: minLength(6)
    },
    repeatPassword: {
      sameAsPassword: sameAs('newPassword')
    }
  }
}
</script>
