            .app-st-box__main 
                .form 
                    .form__line(v-for="paramItem, paramKey in companyParameters" :key="paramKey")
                        .form-group(v-if="paramItem.type === 'TEXT'")
                            .form-group__title {{paramItem.name}} ({{paramItem.description}})
                            .form-group__main
                                input(
                                    type='text' 
                                    v-model="formData[paramItem.name]" 
                                    v-validate="'required'"
                                ).st-input

                        .form-group(v-if="paramItem.type === 'NUMERIC'")
                            .form-group__title {{paramItem.name}} ({{paramItem.description}})
                            .form-group__main
                                input(
                                    type='text' 
                                    v-model="formData[paramItem.name]"
                                    v-validate="'required'"
                                ).st-input

                        .form-group(v-if="paramItem.type === 'CATEGORICAL'")
                            .form-group__title {{paramItem.name}} ({{paramItem.description}})
                            .form-group__main
                                select.st-select(
                                    v-model="formData[paramItem.name]"
                                    v-validate="'required'"
                                )
                                    option(v-for="optionItem, optionKey in paramItem.possibleValues" :key="optionKey" :value="optionItem") {{fieldsNames[optionItem]}}

                        .form-group(v-if="paramItem.type === 'DATE'")
                            .form-group__title {{paramItem.name}} ({{paramItem.description}})
                            .form-group__main
                                input(
                                    type='text' 
                                    v-model="formData[paramItem.name]"
