<script>
import ApiService from "@/services/api.service"
import fieldsNames from '@/enums/fieldsNames'


export default {
    name: "Scoring",  
    data() {
        return {
            formData: {},
            fieldsNames: fieldsNames
        }
    },
    computed: {
        companyParameters() {
            return this.$store.getters.getState('companyParameters')
        },
    },
    methods: {
        sendForm() {
            this.$validator.validateAll().then(result => {
                if(result) {

                    let companyId = localStorage.getItem("companyId")

                    let reqData = {
                        companyId: companyId,
                        paramaters: this.formData 
                    }
                    
                    ApiService.post(`scoring/new`, reqData).then(res => {
                        console.log(res)
                    }).catch(rej => {
                        this.$notify({
                            group: 'default',
                            title: 'Ошибка',
                            type: 'error',
                            text: 'Не удалось отправить данные'
                        });
                    })
                }
            })
        }
    }
};
</script>
